// Your existing ActionLink component with slight modifications to destructure props
const ActionLink = ({ url, icon, children }) => {
    return (
        <span className={"text-center inline-block my-3.5 sm:my-2 mx-2"}>
            <a href={url} target={!url.startsWith("#") ? "_blank" : "_self"}
                rel="noopener noreferrer" // For security reasons, add rel="noopener noreferrer" when using target="_blank"
                className="text-xl no-underline font-normal text-[#009cff] bg-[#f9f9f9] hover:bg-[#f4f4f4] hover:transition-all px-4 py-3 rounded-xl">
                <span className="align-middle inline-flex justify-center mr-0.25">{icon}&nbsp;</span>
                {children}
            </a>
        </span>
    )
};

// New component for displaying the BibTeX as a button and showing the citation text
const BibTexButton = ({ icon }) => {
    const [showBibtex, setShowBibtex] = useState(false);
    const bibtexCitation = `@article{yang2023emernerf,
    title={EmerNeRF: Emergent Spatial-Temporal Scene Decomposition via Self-Supervision},
    author={Jiawei Yang and Boris Ivanovic and Or Litany and Xinshuo Weng and Seung Wook Kim and Boyi Li and Tong Che and Danfei Xu and Sanja Fidler and Marco Pavone and Yue Wang},
    journal={arXiv preprint arXiv:2311.02077},
    year={2023}
}`;

    const handleClick = (e) => {
        e.preventDefault(); // This will prevent the default action of the anchor tag
        setShowBibtex(!showBibtex);
    };

    return (
        <>
            <span className={"text-center inline-block my-3.5 sm:my-2 mx-2"}>
                <a href="#" onClick={handleClick}
                    className="text-xl no-underline font-normal text-[#009cff] bg-[#f9f9f9] hover:bg-[#f4f4f4] hover:transition-all px-4 py-3 rounded-xl">
                    <span className="align-middle inline-flex justify-center mr-0.25">{icon}&nbsp;</span>
                    BibTeX
                </a>
            </span>
            {showBibtex && (
                <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex justify-center items-center z-50">
                    <div className="bg-white p-4 rounded-lg shadow-lg text-sm">
                        <pre style={{ fontSize: '0.75rem' /* This sets the font size to 20px */ }}>
                            <code>
                                {bibtexCitation}
                            </code>
                        </pre>
                        <button onClick={() => setShowBibtex(false)}>Close</button>
                    </div>
                </div>
            )}
        </>
    );
};


