const CarouselItem: React.FC<{ children: React.ReactNode, video: string }> = ({ children, video }) => {
    const [isInView, setIsInView] = useState(false);
    const videoRef = useRef(null);

    useEffect(() => {
        const observer = new IntersectionObserver(entries => {
            entries.forEach(entry => {
                setIsInView(entry.isIntersecting);
            });
        });

        observer.observe(videoRef.current);

        return () => observer.disconnect();
    }, []);

    return (
        <div>
            <video
                ref={videoRef}
                controls
                autoPlay={isInView}
                muted
                playsInline
                loop
                alt={video}
                className="carousel-video px-1.5 rounded-xl">
                {isInView && <source src={video} type="video/mp4" />}
            </video>
            <p className="text-center">{children}</p>
        </div>
    )
}

interface FlowDemoProps {
    children: React.ReactNode;
    id: string;
    video: string;
    plotData: {
        data: any;
        layout: any;
    };
    hidden?: boolean;
}
const FlowDemo: React.FC<FlowDemoProps> = ({ children, id, video, plotData, hidden }) => {
    const [isInView, setIsInView] = useState(false);
    const videoRef = useRef(null);

    useEffect(() => {
        const observer = new IntersectionObserver(entries => {
            entries.forEach(entry => {
                setIsInView(entry.isIntersecting);
            });
        });

        observer.observe(videoRef.current);

        return () => observer.disconnect();
    }, []);

    return (
        <div id={id} className={"demo-component" + (hidden ? " hidden" : "")}>
            <video
                ref={videoRef}
                autoPlay muted playsInline loop alt={children}
                style={{ width: '100%', display: 'block', maxWidth: 'none', marginBottom: 0 }}
                className="rounded-lg border border-gray-300">
                <source src={video} type="video/mp4" />
            </video>
            {isInView && (
                <Plot
                    data={plotData.data}
                    layout={plotData.layout}
                    style={{ marginTop: 0, display: 'block', maxWidth: '100%', width: "1100px" }}
                    className="rounded-lg"
                />
            )}
            <div className="text-left mt-2 text-base text-gray-600">
                <sup>*</sup>Interact with the plot using the mouse. To optimize page load times, results are displayed every second, showcasing a sampled 1/6 of the points per frame.
            </div>
        </div>
    );
};

